import { combineReducers } from 'redux';
import { OPEN_MODAL, CLOSE_MODAL } from '../actions';

const open = (state = false, action) => {
  switch (action.type) {
    case OPEN_MODAL:
      return true;
    case CLOSE_MODAL:
      return false;
    default:
      return state;
  }
};

const info = (state = {}, action) => {
  switch (action.type) {
    case OPEN_MODAL:
      return action.payload.data;
    default:
      return state;
  }
};

export default combineReducers({
  open,
  info
});



